.footer {
  display: flex;
  justify-content: space-between;
  margin: 80px 40px 40px;
}

.footer__content {
  margin-bottom: 40px;
  font-size: 16px;
  font-weight: 600;
}

.footer__button {
  background-color: #fff;
  border: none;
  font-weight: 700;
  font-size: 20px;
}

.footer__button:hover {
  cursor: pointer;
}
